import React from "react";
import { getAllPhotographers } from "../../app/lib/prisma-db.js";

export default async function PhotographersList() {
  const photographers = await getAllPhotographers();

  return (
    <div className="grid-container">
      {photographers.map(p => (
        <div key={p.id} className="photographer-card">
          <div className="photo">
            <Image src={`/assets/${p.portrait}`} width={150} height={150} alt={p.name} className="rounded-full" />
          </div>
          <div className="infos">
            <h2>{p.name}</h2>
            <p>{p.city} - {p.country}</p>
            <p>{p.tagline}</p>
            <p>{p.price}â‚¬/jour</p>
          </div>
        </div>
      ))}
    </div>
  );
}
